export function isFloat(value) {
    return value !== Math.floor(value);
}
export function isObject(obj) {
    return obj !== null && typeof obj === 'object' && !Array.isArray(obj);
}
export function isProperty(obj, key) {
    return isObject(obj) && Object.prototype.hasOwnProperty.call(obj, key);
}
export function isVariable(value) {
    return (typeof value === 'string' ||
        typeof value === 'number' ||
        typeof value === 'boolean');
}
export function isVariables(obj) {
    if (!isObject(obj))
        return false;
    for (const key of Object.keys(obj)) {
        const value = obj[key];
        if (!(isVariable(value) || isVariables(value)))
            return false;
    }
    return true;
}
export function isAttributeName(name, element) {
    return typeof name === 'string' && name in element;
}
export function isAttributeValue(value) {
    return (typeof value === 'string' ||
        typeof value === 'number' ||
        typeof value === 'boolean');
}
export function isEventType(type, element) {
    return (typeof type === 'string' &&
        type.startsWith('on') &&
        type.slice(2).toLowerCase() in element);
}
export function isEventCallback(callback) {
    return typeof callback === 'function';
}
export function isChild(child) {
    return child instanceof Node || typeof child === 'string';
}
export function isChildren(name, children) {
    return (name === 'children' &&
        (Array.isArray(children) ? children.every(isChild) : isChild(children)));
}
