import { isAttributeName, isAttributeValue, isChildren, isEventCallback, isEventType, isObject, isProperty, } from './checks';
export function createClone(element, index = 0, deep = true) {
    const node = typeof element === 'function' ? element(index) : element;
    return node.cloneNode(deep);
}
export function createElement(props = {}) {
    const { tag = 'div', ...rest } = props;
    const attributes = rest;
    const element = document.createElement(tag);
    for (const name of keys(attributes)) {
        if (!isProperty(attributes, name))
            continue;
        const value = attributes[name];
        if (isEventType(name, element)) {
            const type = name.slice(2).toLowerCase();
            if (isEventCallback(value)) {
                element.addEventListener(type, value);
            }
        }
        else if (isChildren(name, value)) {
            const children = Array.isArray(value) ? value : [value];
            element.append(...children);
        }
        else if (isAttributeName(name, element) && isAttributeValue(value)) {
            element.setAttribute(name === 'className' ? 'class' : name, value);
        }
    }
    return element;
}
export function createElements(length = 1, element = createElement(), deep = true) {
    return Array.from({ length: length < 1 ? 1 : length }, (_, index) => createClone(element, index, deep));
}
export function cssVariables(variables, prefix = '') {
    const result = [];
    for (const [key, value] of Object.entries(variables)) {
        const variable = prefix === '' ? key : `${prefix}-${key}`;
        if (isAttributeValue(value)) {
            result.push(`--${variable}:${value.toString()}`);
        }
        else if (isObject(value)) {
            result.push(cssVariables(value, variable));
        }
    }
    return result.join(';');
}
export function keys(obj) {
    return Object.keys(obj);
}
export function values(obj) {
    return Object.values(obj);
}
export function entries(obj) {
    return Object.entries(obj);
}
